import React, {Component} from 'react';
import social_image from '../images/footer-social.png';
import powered_by from '../images/powered-by.png';

class Footer extends Component {
    render() {
        return(
            <footer>
                <div className="footer text-center">
                    <div className="container">
                        <img src={social_image} className="d-block m-auto" />
                        <img src={powered_by} className="d-block m-auto" />
                    </div>
                </div>
            </footer>
        )
    }
}

export default Footer;